*{ padding: 0; margin: 0; border: 0; font-size: 18px; line-height: 24px; cursor: default; }
*,*:before,*:after{ -moz-box-sizing: border-box; -webkit-box-sizing: border-box; box-sizing: border-box; }
:focus,:active{outline: none;}
a:focus,a:active{outline: none;}
nav,footer,header,aside{display: block;}
html,body{
	height: 100%;
	width: 100%;
	font-size: inherit;
	line-height: inherit;
	-ms-text-size-adjust: 100%;
	-moz-text-size-adjust: 100%;
	-webkit-text-size-adjust: 100%;
	font-family: 'Ubuntu', sans-serif;
}
table,th,td {font-weight: 400; text-align: center;}
input,button,textarea{font-family:inherit;}
input::-ms-clear{display: none;}
input{
	&::-webkit-input-placeholder { color: #212122; }
	&::-moz-placeholder { color: #212122; }
}
button{cursor: pointer; * {cursor: pointer;}}
button::-moz-focus-inner {padding:0;border:0;}
a, a:visited{text-decoration: none;color: #000}
a:hover{text-decoration: none;}
ul li{list-style: none;}
img{vertical-align: top;}
h1,h2,h3,h4,h5,h6{font-size:inherit;font-weight: 400;}
svg { transition: .3s; }